import Skills from "../Styles/Skills.css"
import React from 'react'
import {motion} from "framer-motion"
const Skill = () => {
    return (
        <>
        {/* style={{ width: "50%" }} style={{ width: "50%" }} */}
            <h1 className="skill">Skills</h1>
            <div id="skillcontainer">
                <div className="container">
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:0.5}} className="logo"> <img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/html-5.svg" alt="" /> </motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:1}} className="logo"><img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/css3.svg" alt="" /></motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:1}} className="logo"><img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/javascript.svg" alt="" /></motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:0.5}} className="logo"><img className="img"  src="https://chiranjeev-thapliyal.vercel.app/svg/reactjs.svg" alt="" /></motion.div>

                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:0.5}} className="logo"><img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/redux.svg" alt="" /></motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:1}} className="logo"><img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/nodejs.svg" alt="" /></motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:1}} className="logo"><img className="img" src="https://chiranjeev-thapliyal.vercel.app/svg/mongodb.svg" alt="" /></motion.div>
                    <motion.div whileInView={{opacity:[0,1]}} transition={{duration:0.5}} className="logo"><img className="img"  src="https://cdn-icons-png.flaticon.com/512/25/25231.png" alt="" /></motion.div>
                </div>logo
            </div>
        </>
    )
}

export default Skill